import React, { useState, useEffect, createContext } from 'react';
import { isAuthenticated } from '../../../../service/AuthService';
import Login from './Login';

export const LoginContext = createContext();

export const LoginProvider = ({ children }) => {
    const [currentUser, setCurrentUser] = useState(null);
    const [superAdmin, setSuperAdmin] = useState(false);
    const [administrador, setAdministrador] = useState(false);
    const [autorizador, setAutorizador] = useState(false);
    const [solicitante, setSolicitante] = useState(false);
    const [activeIndex, setActiveIndex] = useState(0);
    const [configuracion, setConfiguracion] = useState({});

    // Valores predeterminados de la configuración
    const defaultValuesConfiguracion = {
        periodo: null,
        subPeriodo: null,
        lstNivel: null,
        lstCarrera: null,
    };

    // Verifica si el usuario está autenticado
    useEffect(() => {
        const checkLoggedIn = async () => {
            let cuser = isAuthenticated();
            if (cuser === null) {
                sessionStorage.setItem('user', '');
                cuser = '';
            }
            setCurrentUser(cuser); // Aquí se decodifica el user, el cual es el token
        };

        checkLoggedIn();
    }, []);

    // Función para manejar los roles de seguridad
    const handleSecurity = () => {
        const token = currentUser?.token;
        if (token) {
            setSuperAdmin(token?.['Super admin'] || false);
            setAdministrador(token?.Administrador || false);
            setAutorizador(token?.Autorizador || false);
            setSolicitante(token?.Solicitante || false);
        }
    };

    return (
        <LoginContext.Provider
            value={{
                currentUser,
                setCurrentUser,
                superAdmin,
                administrador,
                autorizador,
                solicitante,
                handleSecurity,
                activeIndex,
                setActiveIndex,
                configuracion,
                setConfiguracion,
                defaultValuesConfiguracion,
            }}
        >
            {currentUser?.token ? children : <Login />}
        </LoginContext.Provider>
    );
};

export default LoginContext;
